body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

#root {
    background: #434042;
    overflow: hidden;
    background-image: url("img/BackgroundOfficeMountainsEpic.webp");
    background-color: rgba(0, 0, 0, 70%);
    background-blend-mode: darken;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: 25% 40%;
    background-size: cover;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.App {
    text-align: center;
    background-image: url("img/BackgroundOfficeMountainsEpic.webp");
    background-color: rgba(0, 0, 0, 70%);
    background-blend-mode: darken;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: 25% 40%;
    background-size: cover;
    overflow: hidden;
    transition: all 2s ease-in-out;
    transform: rotate(0deg);
}

.App:hover {
    filter: none;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

@keyframes do-a-barrel-roll {
    from {
        transform: rotate(0deg);
    }
    10% {
        transform: rotate(-30deg);
    }
    19% {
        transform: rotate(60deg);
    }
    30% {
        transform: rotate(-80deg);
    }
    to {
        transform: rotate(5400deg);
    }
}

.barrel-roll {
    animation: do-a-barrel-roll infinite 14s ease-in-out;
}

.App-header {
    font-size: 19px;
    color: white;
    padding: 15px 10px 15px 10px;
    flex-wrap: wrap;
    align-content: flex-start;
    gap: 1vh;
    min-height: calc(100vh - 30px);
    transition: all 500ms ease-in-out;
}

.item-max-header {
    background-color: rgba(0, 0, 0, 0.5);
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 30px;
    flex-direction: row;
    padding: 7px 15px 7px 15px;
    margin-bottom: 4px;
    max-height: 33px;
    transition: all 500ms ease-in-out;
}

.menu-hidden {
    padding-top: 0;
    padding-bottom: 0;
    margin-bottom: 0;
    max-height: 0;
    overflow: hidden;
}

.menu-item {
    transition: all 500ms ease-in-out;
    user-select: none;
    font-family: ahganirya;
    font-size: 20px;
    letter-spacing: 1.2px;
}

.simple-notes-wrapper {
    margin-top: 14px;
    margin-bottom: 8px;
    background-color: rgba(0,0,0, 0.3);
    border-radius: 10px;
    padding-top: 5px;
    padding-bottom: 8px;
    text-align: left;
    position: relative;
    border: 1px solid #616161;
}

.simple-notes-label {
    position: absolute;
    left: -6px;
    top: -15px;
    padding-left: 5px;
    padding-top: 1px;
    background-color: rgba(0,0,0, 0.3);
    padding-right: 7px;
    border-radius: 7px;
    margin-left: 13px;
    margin-top: 0;
    background-color: #1d1e1e;
    border: 1px solid #616161;
}

.lock-round-icon-wrapper {
    position: absolute;
    top: 5px;
    right: 3px;
    transition: all 500ms ease-in-out;
    opacity: 0;
    z-index: 100;
}

.lock-round-icon {
    width: 40px;
    border: 2px solid white;
    border-radius: 50%;
}

.simple-notes-label {
    transition: all 0.5s ease-in-out;
}

.menu-hidden .lock-round-icon-wrapper {
    /*display: block !important;*/
    opacity: 1 !important;
}

.menu-item-text-style {
    transition: all 500ms ease-in-out;
    user-select: none;
    font-family: ahganirya;
    font-size: 20px;
    letter-spacing: 1.2px;
}

.menu-hidden .menu-item {
    opacity: 0;
}

.bro-three-letters {
    margin-top: -2px;
    border: 2px solid #fff;
    border-radius: 8px;
    position: absolute;
    transition: all .5s ease-in-out;
    user-select: none;
    z-index: 100;
    padding-left: 5px;
    padding-right: 4px;
    font-size: 20px;
}

.menu-hidden .bro-three-letters {
    margin-top: 9px;
    margin-left: -25px;
    transform: rotate(-35deg);
    font-size: 21px;
    background-color: #000;
}

.menu-item-border {
    border: 2px solid rgba(255, 255, 255, 0.2);;
    border-radius: 16px;
    padding: 3px 30px;
    /*display: inline-block;*/
}

.menu-item-to-dev {
    margin-left: 36px;
    width: 100px;
}

.bro-go-dev {
    border: 2px solid #fff;
}

.launcher-text {
    padding-left: 65px;
}

.filter-gray {
    filter: grayscale(80%);
}

.modal {
    position: fixed;
    width: 400px;
    z-index: 1040;
    top: calc(60% - 400px);
    left: calc(60% - 400px);
    background-color: white;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 20px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
}

.backdrop {
    position: fixed;
    z-index: 1040;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #000;
    opacity: 0.5;
}

.modal-title {
    font-weight: 500;
    font-size: 1.25rem;
    margin: auto;
    text-align: center;
}

.modal-header {
    border-bottom: 1px solid #e9ecef;
    display: flex;
    justify-content: space-between;
}

.modal-desc,
.modal-header,
.modal-footer {
    padding: 12px;
}

.close-button {
    font-size: 1.5rem;
    font-weight: 700;
    line-height: 1;
    color: #000;
    border: none;
    background-color: white;
    opacity: 0.7;
    cursor: pointer;
}

button {
    cursor: pointer;
    font-size: 1rem;
    padding: 10px 12px;
    border-radius: 5px;
    border: none;
}

.modal-footer {
    border-top: 1px solid #e9ecef;
    display: flex;
    justify-content: flex-end;
    gap: 8px;
}

.secondary-button {
    color: #fff;
    background-color: #dc3545;
}

.primary-button {
    color: #fff;
    background-color: #28a745;
}

.modal-example {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.image-style-selector-block {
    margin-top: 20px;
    width: 100%;
}

.input-style {
    width: 97%;
}

.import-button {
    display: none;
}

.y-wrapper {
    width: 150px;
    background-color: rgba(255, 255, 255, 50%);
    padding: 10px;
    border-radius: 20px;
    margin-left: 20px;
}

.g-wrapper {
    width: 150px;
    padding: 10px;
    border-radius: 20px;
    margin-left: 20px;
    align-self: center;
}


.y-img {
    height: auto;
    width: 100%;
}

.youtube-search-container {
    border-radius: 20px;
    /*background-color: #aaa;*/
    background-color: rgba(0, 0, 0, 0%);
    height: 45px;
    margin-left: 20px;
    margin-right: 20px;
    width: 100%;
    border: 1px solid #bbb;
}

.youtube-search {
    width: calc(100% - 20px);
    background-color: rgba(0, 0, 0, 0%);
    border-width: 0;
    font-size: 35px;
    color: #aaa;
}

.youtube-search:focus {
    outline: none;
}

.gt-img {
    width: 98px;
}

/* Width */
::-webkit-scrollbar {
    width: 0;
}

/* Track */
::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0);
    padding: 2px;
}

/* Track */
::-webkit-scrollbar {
    background: rgba(255, 255, 255, 0);
    max-height: 12px;
    max-width: 5px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: orange;
    border-radius: 20px;
    max-height: 5px;
    max-width: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: red;
    max-height: 5px;
    max-width: 5px;
}

.logo-version-text {
    margin-left: 6px;
    font-size: 11px;
}

.gh-icon-in-header {
    width: 25px;
    position: fixed;
    margin-left: 5px;
}

.gh-icon-in-header-dev {
    width: 25px;
    margin-left: 85px;
    position: fixed;
}

.context-menu-nothing {
    text-align: center;
}

.close-menu-button {
    position: absolute;
    margin-left: -21px;
    margin-top: -34px;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background-size: 66%;
    background-repeat: no-repeat;
    background-position: center;
    background-color: #000;
    transform: rotate(354deg);
    border: 1px solid #888;
}

input[type="checkbox"] {
    accent-color: rebeccapurple;
    transition: all 500ms ease-in-out;
}

.ssl-warning-message {
    background-color: #ffc107;
    padding-top: 3px;
    padding-bottom: 6px;
    border-bottom: 1px solid black;
    display: flex;
    justify-content: center;
    padding-left: 20px;
    padding-right: 20px;
}

.ssl-warning-message_first {
    align-self: center;
}

.small-flag {
    width: 38px;
    height: 20px;
    border: 1px solid black;
    margin-top: 3px;
    margin-right: 10px;
}

.small-flag:first-child {
    margin-left: 0;
}

.small-flag_latest {
    margin-right: 40px;
}

.splide-content {
    height: 100px;
}

.splide-thumbnail {
    height: 100px;
}

.splide-thumbnail-button {
    padding: 0;
}

.splide-thumbnail-button {
    background-color: unset;
}

.thumbnails li button div {
    color: white;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

.thumbnails li:not(:last-child) {
    margin-right: 10px;
}

.thumbnails li:first-child {
    margin-left: 15px;
}

.thumbnails li {
    padding-left: 10px;
    padding-right: 10px;
    border-top: 1px solid #616161;
    border-left: 1px solid #616161;
    border-right: 1px solid #616161;
    border-top-left-radius: 13px;
    border-top-right-radius: 13px;
    padding-top: 3px;
    background-color: rgba(0, 0, 0, 0.4);
}

.thumbnails {
    display: flex;
    list-style-type: none;
    margin-top: 7px !important;
    margin-bottom: 10px !important;
    padding-left: 0;
}

.splide__track {
    overflow: visible !important;
}

.tab-backstage {
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0) 100%);
    height: 666px;
    position: relative;
    top: -10px;
    left: -7px;
    width: calc(100% + 12px);
    margin-bottom: -666px;
    border-top-left-radius: 24px;
    border-top-right-radius: 24px;
    border-left: 1px solid #616161;
    border-right: 1px solid #616161;
    border-top: 1px solid #616161;
}

.thumbnails li:first-child {
    transition: margin-left 0.5s ease-in-out;
}

.thumbnail.thumbnail_active {
    border-bottom: 1px solid #181717;
    margin-bottom: -1px;
    z-index: 9999;
}
